.App {
  max-width: 1600px;
  margin: 0 auto;
  padding: 30px;
  display: grid;
  grid-template-columns: 250px 1fr;
  box-sizing: border-box;
  grid-gap: 80px;
}

.wrapper {
  padding: 20px 40px;
  background-color: white;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  margin-bottom: 40px;
}

.left {
  /* width: 300px; */
}

.right {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 40px;
}

.tip {  
  color: gray;
  font-style: italic;
}

.question {
  margin-bottom: 40px;
}

.question_text {
  margin-bottom: 20px;
}

.answer {
  cursor: pointer;
  margin-bottom: 10px;
}

.block {
  margin-bottom: 50px;
}

.links {
  margin-left: 20px;
  cursor: pointer;
}

.links:hover{
  color: blue;
}


.item {
  /* padding: 10px 30px; */
  /* border: 1px solid gray; */
  margin-bottom: 20px;
  cursor: pointer;
}

.item:hover {
  color: #f05023;
}

.message {
  color: #f05023;
  font-weight: 700;
  margin-right: 40px;
}

.checked {
  color: #f05023;
  font-weight: 700;
}

.ReactModal__Overlay {
  background-color: rgba(63, 63, 63, 0.95) !important
}

.button {
  padding: 10px 25px;
  border: 1px solid gray;
  cursor: pointer;
}